import { useState } from "react";
import { useTranslation } from "react-i18next";
import { useNavigate } from "react-router-dom";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { useCouponTemplates, useDeleteCouponTemplate, type CouponTemplate } from "@/hooks/useCoupons";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { useToast } from "@/hooks/use-toast";
import { supabase } from "@/integrations/supabase/client";

import { CouponFormDialog } from "@/components/admin/CouponFormDialog";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
} from "@/components/ui/alert-dialog";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
} from "@/components/ui/dialog";
import { Plus, Search, Ticket, Percent, Euro, Trash2, Edit, Eye, Mail, Loader2 } from "lucide-react";
import { format } from "date-fns";

const AdminCoupons = () => {
  const { t } = useTranslation();
  const { toast } = useToast();
  const navigate = useNavigate();
  const { data: couponTemplates, isLoading } = useCouponTemplates();
  const deleteMutation = useDeleteCouponTemplate();
  const { getLocalizedField } = useLocalizedContent();
  const [searchQuery, setSearchQuery] = useState("");
  const [formDialogOpen, setFormDialogOpen] = useState(false);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedTemplate, setSelectedTemplate] = useState<CouponTemplate | null>(null);
  
  // 清除邮箱记录相关状态
  const [clearEmailDialogOpen, setClearEmailDialogOpen] = useState(false);
  const [emailToClear, setEmailToClear] = useState("");
  const [isClearingEmail, setIsClearingEmail] = useState(false);

  const filteredTemplates = couponTemplates?.filter((template) => {
    const title = String(getLocalizedField(template, "title") || "");
    const query = searchQuery.toLowerCase();
    
    return title.toLowerCase().includes(query);
  });

  const handleDelete = () => {
    if (selectedTemplate) {
      deleteMutation.mutate(selectedTemplate.id, {
        onSuccess: () => {
          setDeleteDialogOpen(false);
          setSelectedTemplate(null);
        },
      });
    }
  };

  const handleClearEmailRecords = async () => {
    if (!emailToClear.trim()) {
      toast({
        title: t("common.error"),
        description: t("admin.clearEmail.enterEmail"),
        variant: "destructive",
      });
      return;
    }

    setIsClearingEmail(true);
    try {
      const { data, error } = await supabase
        .from("user_coupons")
        .delete()
        .eq("claimed_email", emailToClear.trim().toLowerCase());

      if (error) throw error;

      toast({
        title: t("common.success"),
        description: t("admin.clearEmail.success", { email: emailToClear }),
      });
      
      setClearEmailDialogOpen(false);
      setEmailToClear("");
    } catch (error: any) {
      toast({
        title: t("common.error"),
        description: error.message,
        variant: "destructive",
      });
    } finally {
      setIsClearingEmail(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
        <Card>
          <CardHeader>
            <div className="flex flex-col sm:flex-row justify-between items-start sm:items-center gap-4">
              <div>
                <CardTitle className="flex items-center gap-2">
                  <Ticket className="w-6 h-6" />
                  {t("admin.couponTemplates.title")}
                </CardTitle>
                <CardDescription>
                  {t("admin.couponTemplates.description")}
                </CardDescription>
              </div>
              <div className="flex gap-2 flex-wrap">
                <Dialog open={clearEmailDialogOpen} onOpenChange={setClearEmailDialogOpen}>
                  <DialogTrigger asChild>
                    <Button variant="outline">
                      <Mail className="w-4 h-4 mr-2" />
                      {t("admin.clearEmail.title")}
                    </Button>
                  </DialogTrigger>
                  <DialogContent>
                    <DialogHeader>
                      <DialogTitle>{t("admin.clearEmail.dialogTitle")}</DialogTitle>
                      <DialogDescription>
                        {t("admin.clearEmail.dialogDescription")}
                      </DialogDescription>
                    </DialogHeader>
                    <div className="space-y-4 py-4">
                      <div className="space-y-2">
                        <Label htmlFor="email-to-clear">{t("admin.clearEmail.emailLabel")}</Label>
                        <Input
                          id="email-to-clear"
                          type="email"
                          placeholder="example@email.com"
                          value={emailToClear}
                          onChange={(e) => setEmailToClear(e.target.value)}
                        />
                      </div>
                    </div>
                    <DialogFooter>
                      <Button
                        variant="outline"
                        onClick={() => {
                          setClearEmailDialogOpen(false);
                          setEmailToClear("");
                        }}
                      >
                        {t("common.cancel")}
                      </Button>
                      <Button
                        variant="destructive"
                        onClick={handleClearEmailRecords}
                        disabled={isClearingEmail || !emailToClear.trim()}
                      >
                        {isClearingEmail && <Loader2 className="w-4 h-4 mr-2 animate-spin" />}
                        {t("admin.clearEmail.confirm")}
                      </Button>
                    </DialogFooter>
                  </DialogContent>
                </Dialog>
                <Button onClick={() => {
                  setSelectedTemplate(null);
                  setFormDialogOpen(true);
                }}>
                  <Plus className="w-4 h-4 mr-2" />
                  {t("admin.couponTemplates.create")}
                </Button>
              </div>
            </div>
          </CardHeader>
          <CardContent>
            <div className="mb-6">
              <div className="relative">
                <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-muted-foreground w-4 h-4" />
                <Input
                  placeholder={t("admin.couponTemplates.search")}
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-10"
                />
              </div>
            </div>

            {isLoading ? (
              <div className="text-center py-8 text-muted-foreground">
                {t("admin.couponTemplates.loading")}
              </div>
            ) : filteredTemplates && filteredTemplates.length > 0 ? (
              <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
                {filteredTemplates.map((template) => (
                  <Card key={template.id} className="hover:shadow-lg transition-shadow">
                    <CardHeader>
                      <div className="flex justify-between items-start">
                      <div className="flex-1">
                          <CardTitle className="text-lg mb-1">
                            {getLocalizedField(template, "title")}
                          </CardTitle>
                          <CardDescription className="text-sm">
                            {getLocalizedField(template, "description")}
                          </CardDescription>
                        </div>
                        <div className="flex flex-col items-end gap-1 ml-2">
                          <Badge variant={template.is_active ? "default" : "secondary"}>
                            {template.is_active ? t("admin.couponTemplates.active") : t("admin.couponTemplates.inactive")}
                          </Badge>
                          {template.auto_link_activities && (
                            <Badge variant="outline" className="text-xs">
                              {t("admin.couponTemplates.autoLink")}
                            </Badge>
                          )}
                        </div>
                      </div>
                    </CardHeader>
                    <CardContent>
                      <div className="space-y-3">
                        <div className="flex items-center justify-between">
                          <span className="text-sm text-muted-foreground">{t("admin.couponTemplates.discountType")}</span>
                          <div className="flex items-center gap-1">
                            {template.discount_type === "percentage" ? (
                              <>
                                <Percent className="w-4 h-4" />
                                <span className="font-semibold">{template.discount_value}%</span>
                              </>
                            ) : (
                              <span className="font-semibold">€{template.discount_value}</span>
                            )}
                          </div>
                        </div>

                        <div className="flex items-center justify-between">
                          <span className="text-sm text-muted-foreground">{t("admin.couponTemplates.createdAt")}</span>
                          <span className="text-sm">
                            {format(new Date(template.created_at), "yyyy-MM-dd")}
                          </span>
                        </div>

                        <div className="flex gap-2 pt-3 border-t">
                          <Button
                            variant="outline"
                            size="sm"
                            className="flex-1"
                            onClick={() => navigate(`/admin/coupons/${template.id}`)}
                          >
                            <Eye className="w-4 h-4 mr-1" />
                            {t("admin.couponTemplates.view")}
                          </Button>
                          <Button
                            variant="outline"
                            size="sm"
                            className="flex-1"
                            onClick={() => {
                              setSelectedTemplate(template);
                              setFormDialogOpen(true);
                            }}
                          >
                            <Edit className="w-4 h-4 mr-1" />
                            {t("admin.couponTemplates.edit")}
                          </Button>
                          <Button
                            variant="destructive"
                            size="sm"
                            onClick={() => {
                              setSelectedTemplate(template);
                              setDeleteDialogOpen(true);
                            }}
                          >
                            <Trash2 className="w-4 h-4" />
                          </Button>
                        </div>
                      </div>
                    </CardContent>
                  </Card>
                ))}
              </div>
            ) : (
              <div className="text-center py-12">
                <Ticket className="w-12 h-12 mx-auto text-muted-foreground mb-4" />
                <p className="text-muted-foreground mb-4">
                  {searchQuery ? t("admin.couponTemplates.noMatch") : t("admin.couponTemplates.empty")}
                </p>
                <Button onClick={() => setFormDialogOpen(true)}>
                  <Plus className="w-4 h-4 mr-2" />
                  {t("admin.couponTemplates.createFirst")}
                </Button>
              </div>
            )}
          </CardContent>
        </Card>

        <CouponFormDialog
          key={selectedTemplate?.id || "new"}
          open={formDialogOpen}
          onOpenChange={(open) => {
            setFormDialogOpen(open);
            if (!open) {
              setSelectedTemplate(null);
            }
          }}
          template={selectedTemplate}
        />

        <AlertDialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>{t("admin.couponTemplates.confirmDelete")}</AlertDialogTitle>
              <AlertDialogDescription>
                {t("admin.couponTemplates.deleteWarning", { name: selectedTemplate ? getLocalizedField(selectedTemplate, "title") : "" })}
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel>{t("common.cancel")}</AlertDialogCancel>
              <AlertDialogAction onClick={handleDelete}>
                {t("admin.couponTemplates.delete")}
              </AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>
    </div>
  );
};

export default AdminCoupons;
