import { useEffect } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useToast } from "@/hooks/use-toast";
import { useTranslation } from "react-i18next";
import { useSaveCouponTemplate, type CouponTemplate } from "@/hooks/useCoupons";
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
} from "@/components/ui/dialog";
import {
  Form,
  FormControl,
  FormDescription,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from "@/components/ui/form";
import { Input } from "@/components/ui/input";
import { Button } from "@/components/ui/button";
import { Textarea } from "@/components/ui/textarea";
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import { Switch } from "@/components/ui/switch";
import { useQuery } from "@tanstack/react-query";
import { supabase } from "@/integrations/supabase/client";

const formSchema = z.object({
  activity_id: z.string().min(1, "请选择关联活动"),
  title_zh: z.string().min(1, "中文标题不能为空"),
  title_en: z.string().min(1, "英文标题不能为空"),
  title_pt: z.string().min(1, "葡萄牙语标题不能为空"),
  description_zh: z.string().optional(),
  description_en: z.string().optional(),
  description_pt: z.string().optional(),
  discount_type: z.enum(["percentage", "fixed_amount"]).default("percentage"),
  discount_value: z.coerce.number().min(0, "折扣值不能为负数"),
  min_order_amount: z.coerce.number().min(0).optional(),
  max_discount_amount: z.coerce.number().min(0).optional(),
  valid_from: z.string().min(1, "开始日期不能为空"),
  valid_until: z.string().optional(),
  duration_days: z.coerce.number().min(1).optional(),
  duration_mode: z.enum(["end_date", "duration"]).default("end_date"),
  refresh_mode: z.enum(["none", "weekly", "monthly", "yearly"]).default("none"),
  is_active: z.boolean().default(true),
  visible: z.boolean().default(true),
  auto_link_activities: z.boolean().default(false),
});

type FormData = z.infer<typeof formSchema>;

interface CouponFormDialogProps {
  open: boolean;
  onOpenChange: (open: boolean) => void;
  template?: CouponTemplate | null;
}

export function CouponFormDialog({
  open,
  onOpenChange,
  template,
}: CouponFormDialogProps) {
  const { t } = useTranslation();
  const { toast } = useToast();
  const saveMutation = useSaveCouponTemplate();

  console.log("CouponFormDialog opened:", { open, hasTemplate: !!template });

  // 获取所有活动列表
  const { data: activities, isLoading: loadingActivities, error: activitiesError } = useQuery({
    queryKey: ["activities"],
    queryFn: async () => {
      console.log("Fetching activities...");
      try {
        const { data, error } = await supabase
          .from("activities")
          .select("id, title_zh, title_en, title_pt")
          .eq("visible", true)
          .order("created_at", { ascending: false });

        if (error) {
          console.error("Error fetching activities:", error);
          throw error;
        }
        console.log("Activities fetched:", data?.length, data);
        return data || [];
      } catch (err) {
        console.error("Exception in activities query:", err);
        throw err;
      }
    },
    enabled: open, // 只在对话框打开时查询
  });

  // 处理查询错误
  useEffect(() => {
    if (activitiesError) {
      console.error("Activities query error in useEffect:", activitiesError);
      toast({
        title: "加载失败",
        description: "无法加载活动列表，请刷新页面重试",
        variant: "destructive",
      });
    }
  }, [activitiesError, toast]);

  const form = useForm<FormData>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      activity_id: "",
      title_zh: "",
      title_en: "",
      title_pt: "",
      description_zh: "",
      description_en: "",
      description_pt: "",
      discount_type: "percentage" as const,
      discount_value: 0,
      min_order_amount: undefined,
      max_discount_amount: undefined,
      valid_from: new Date().toISOString().split("T")[0],
      valid_until: "",
      duration_days: undefined,
      duration_mode: "end_date" as const,
      refresh_mode: "none" as const,
      is_active: true,
      visible: true,
      auto_link_activities: false,
    },
  });

  // 当 template 变化时更新表单
  useEffect(() => {
    if (template) {
      console.log("Setting form for edit mode:", template.id);
      // 判断使用哪种模式
      const hasDuration = template.duration_days && template.duration_days > 0;
      form.reset({
        activity_id: template.activity_id,
        title_zh: template.title_zh,
        title_en: template.title_en,
        title_pt: template.title_pt,
        description_zh: template.description_zh || "",
        description_en: template.description_en || "",
        description_pt: template.description_pt || "",
        discount_type: template.discount_type as "percentage" | "fixed_amount",
        discount_value: Number(template.discount_value),
        min_order_amount: template.min_order_amount ? Number(template.min_order_amount) : undefined,
        max_discount_amount: template.max_discount_amount ? Number(template.max_discount_amount) : undefined,
        valid_from: template.valid_from.split("T")[0],
        valid_until: template.valid_until?.split("T")[0] || "",
        duration_days: template.duration_days || undefined,
        duration_mode: hasDuration ? "duration" : "end_date",
        refresh_mode: (template.refresh_mode as "none" | "weekly" | "monthly" | "yearly") || "none",
        is_active: template.is_active,
        visible: template.visible,
        auto_link_activities: template.auto_link_activities || false,
      });
    } else if (open && !template) {
      console.log("Setting form for create mode");
      form.reset({
        activity_id: "",
        title_zh: "",
        title_en: "",
        title_pt: "",
        description_zh: "",
        description_en: "",
        description_pt: "",
        discount_type: "percentage" as const,
        discount_value: 0,
        min_order_amount: undefined,
        max_discount_amount: undefined,
        valid_from: new Date().toISOString().split("T")[0],
        valid_until: "",
        duration_days: undefined,
        duration_mode: "end_date" as const,
        refresh_mode: "none" as const,
        is_active: true,
        visible: true,
        auto_link_activities: false,
      });
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [template, open]);

  // 监听 duration_mode 变化，自动计算结束日期或持续天数
  const durationMode = form.watch("duration_mode");
  const validFrom = form.watch("valid_from");
  const validUntil = form.watch("valid_until");
  const durationDays = form.watch("duration_days");

  // 当模式切换时，自动计算另一个值
  useEffect(() => {
    if (durationMode === "duration" && durationDays && validFrom) {
      // 根据持续天数计算结束日期
      const startDate = new Date(validFrom);
      const endDate = new Date(startDate);
      endDate.setDate(endDate.getDate() + durationDays);
      form.setValue("valid_until", endDate.toISOString().split("T")[0]);
    }
  }, [durationDays, validFrom, durationMode, form]);

  useEffect(() => {
    if (durationMode === "end_date" && validUntil && validFrom) {
      // 根据结束日期计算持续天数
      const startDate = new Date(validFrom);
      const endDate = new Date(validUntil);
      const days = Math.ceil((endDate.getTime() - startDate.getTime()) / (1000 * 60 * 60 * 24));
      if (days > 0) {
        form.setValue("duration_days", days);
      }
    }
  }, [validUntil, validFrom, durationMode, form]);

  const onSubmit = async (data: FormData) => {
    try {
      console.log("Form submitted:", data);
      const templateData: Partial<CouponTemplate> = {
        activity_id: data.activity_id,
        title_zh: data.title_zh,
        title_en: data.title_en,
        title_pt: data.title_pt,
        description_zh: data.description_zh || null,
        description_en: data.description_en || null,
        description_pt: data.description_pt || null,
        discount_type: data.discount_type,
        discount_value: data.discount_value,
        min_order_amount: data.min_order_amount || null,
        max_discount_amount: data.max_discount_amount || null,
        valid_from: data.valid_from,
        valid_until: data.valid_until || null,
        duration_days: data.duration_mode === "duration" ? data.duration_days : null,
        refresh_mode: data.refresh_mode,
        is_active: data.is_active,
        visible: data.visible,
        auto_link_activities: data.auto_link_activities,
      };

      if (template?.id) {
        templateData.id = template.id;
      }

      console.log("Saving template data:", templateData);

      saveMutation.mutate(templateData, {
        onSuccess: () => {
          console.log("Save successful");
          toast({
            title: "保存成功",
            description: "优惠券模板已保存",
          });
          onOpenChange(false);
          form.reset();
        },
        onError: (error: any) => {
          console.error("Error saving coupon template:", error);
          toast({
            title: "保存失败",
            description: error.message || "请检查所有必填字段是否正确填写",
            variant: "destructive",
          });
        },
      });
    } catch (error) {
      console.error("Exception in onSubmit:", error);
      toast({
        title: "提交失败",
        description: "表单提交时发生错误",
        variant: "destructive",
      });
    }
  };

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="max-w-3xl max-h-[90vh] overflow-y-auto">
        <DialogHeader>
          <DialogTitle>
            {template ? "编辑优惠券模板" : "创建优惠券模板"}
          </DialogTitle>
          <DialogDescription>
            {template ? "修改优惠券模板信息" : "创建新的优惠券模板，用户分享后自动生成子优惠券"}
          </DialogDescription>
        </DialogHeader>

        {activitiesError ? (
          <div className="p-6 text-center">
            <p className="text-destructive mb-4">加载活动列表失败</p>
            <Button onClick={() => window.location.reload()}>刷新页面</Button>
          </div>
        ) : (
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
              <FormField
                control={form.control}
                name="activity_id"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>关联活动 *</FormLabel>
                    <Select
                      onValueChange={(value) => {
                        console.log("Activity selected:", value);
                        field.onChange(value);
                      }}
                      value={field.value}
                      disabled={loadingActivities}
                    >
                      <FormControl>
                        <SelectTrigger>
                          <SelectValue placeholder={loadingActivities ? "加载中..." : "选择活动"} />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        {loadingActivities ? (
                          <SelectItem value="loading" disabled>
                            加载中...
                          </SelectItem>
                        ) : activities && activities.length > 0 ? (
                          activities.map((activity) => (
                            <SelectItem key={activity.id} value={activity.id}>
                              {activity.title_zh}
                            </SelectItem>
                          ))
                        ) : (
                          <SelectItem value="no-activities" disabled>
                            暂无可用活动，请先创建活动
                          </SelectItem>
                        )}
                      </SelectContent>
                    </Select>
                    <FormDescription>选择此优惠券关联的活动</FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <FormField
                control={form.control}
                name="title_zh"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>中文标题 *</FormLabel>
                    <FormControl>
                      <Input placeholder="新用户专享" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="title_en"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>英文标题 *</FormLabel>
                    <FormControl>
                      <Input placeholder="New User Exclusive" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="title_pt"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>葡萄牙语标题 *</FormLabel>
                    <FormControl>
                      <Input placeholder="Exclusivo Novo Usuário" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
              <FormField
                control={form.control}
                name="description_zh"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>中文描述</FormLabel>
                    <FormControl>
                      <Textarea placeholder="优惠券描述" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="description_en"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>英文描述</FormLabel>
                    <FormControl>
                      <Textarea placeholder="Coupon description" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="description_pt"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>葡萄牙语描述</FormLabel>
                    <FormControl>
                      <Textarea placeholder="Descrição do cupom" {...field} />
                    </FormControl>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <FormField
                control={form.control}
                name="discount_type"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>折扣类型 *</FormLabel>
                    <Select onValueChange={field.onChange} value={field.value}>
                      <FormControl>
                        <SelectTrigger>
                          <SelectValue placeholder="选择折扣类型" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        <SelectItem value="percentage">百分比折扣 (%)</SelectItem>
                        <SelectItem value="fixed_amount">固定金额折扣 (€)</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="discount_value"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>折扣值 *</FormLabel>
                    <FormControl>
                      <Input 
                        type="number" 
                        min="0" 
                        step="0.01"
                        placeholder={form.watch("discount_type") === "percentage" ? "例如: 10 表示10%折扣" : "例如: 5 表示5欧元折扣"}
                        {...field} 
                      />
                    </FormControl>
                    <FormDescription>
                      {form.watch("discount_type") === "percentage" 
                        ? "输入百分比数值 (0-100)" 
                        : "输入固定折扣金额 (欧元)"}
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <FormField
                control={form.control}
                name="min_order_amount"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>最低消费金额 (€)</FormLabel>
                    <FormControl>
                      <Input 
                        type="number" 
                        min="0" 
                        step="0.01"
                        placeholder="例如: 20"
                        {...field} 
                        value={field.value || ""}
                      />
                    </FormControl>
                    <FormDescription>留空表示无限制</FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="max_discount_amount"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>最高折扣金额 (€)</FormLabel>
                    <FormControl>
                      <Input 
                        type="number" 
                        min="0" 
                        step="0.01"
                        placeholder="例如: 10"
                        {...field}
                        value={field.value || ""}
                      />
                    </FormControl>
                    <FormDescription>仅对百分比折扣有效，留空表示无上限</FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>

            {/* 有效期设置 */}
            <div className="space-y-4 p-4 border rounded-lg bg-muted/30">
              <h4 className="font-medium text-sm">有效期设置</h4>
              
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <FormField
                  control={form.control}
                  name="valid_from"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>开始日期 *</FormLabel>
                      <FormControl>
                        <Input type="date" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="duration_mode"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>有效期模式</FormLabel>
                      <Select onValueChange={field.onChange} value={field.value}>
                        <FormControl>
                          <SelectTrigger>
                            <SelectValue placeholder="选择模式" />
                          </SelectTrigger>
                        </FormControl>
                        <SelectContent>
                          <SelectItem value="end_date">指定结束日期</SelectItem>
                          <SelectItem value="duration">指定持续天数</SelectItem>
                        </SelectContent>
                      </Select>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                {form.watch("duration_mode") === "end_date" ? (
                  <FormField
                    control={form.control}
                    name="valid_until"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>结束日期</FormLabel>
                        <FormControl>
                          <Input type="date" {...field} />
                        </FormControl>
                        <FormDescription>留空表示无限期</FormDescription>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                ) : (
                  <FormField
                    control={form.control}
                    name="duration_days"
                    render={({ field }) => (
                      <FormItem>
                        <FormLabel>持续天数</FormLabel>
                        <FormControl>
                          <Input 
                            type="number" 
                            min="1"
                            placeholder="例如: 30"
                            {...field}
                            value={field.value || ""}
                          />
                        </FormControl>
                        <FormDescription>优惠券领取后有效天数</FormDescription>
                        <FormMessage />
                      </FormItem>
                    )}
                  />
                )}
              </div>

              {/* 显示自动计算的值 */}
              {form.watch("duration_mode") === "duration" && form.watch("duration_days") && form.watch("valid_until") && (
                <p className="text-xs text-muted-foreground">
                  自动计算结束日期: {form.watch("valid_until")}
                </p>
              )}
              {form.watch("duration_mode") === "end_date" && form.watch("valid_until") && form.watch("duration_days") && (
                <p className="text-xs text-muted-foreground">
                  自动计算持续天数: {form.watch("duration_days")} 天
                </p>
              )}
            </div>

            {/* 刷新时间设置 */}
            <div className="space-y-4 p-4 border rounded-lg bg-muted/30">
              <h4 className="font-medium text-sm">刷新周期设置</h4>
              <FormField
                control={form.control}
                name="refresh_mode"
                render={({ field }) => (
                  <FormItem>
                    <FormLabel>刷新周期</FormLabel>
                    <Select onValueChange={field.onChange} value={field.value}>
                      <FormControl>
                        <SelectTrigger className="w-full md:w-[200px]">
                          <SelectValue placeholder="选择刷新周期" />
                        </SelectTrigger>
                      </FormControl>
                      <SelectContent>
                        <SelectItem value="none">不刷新 (一次性)</SelectItem>
                        <SelectItem value="weekly">每周</SelectItem>
                        <SelectItem value="monthly">每月</SelectItem>
                        <SelectItem value="yearly">每年</SelectItem>
                      </SelectContent>
                    </Select>
                    <FormDescription>
                      {field.value === "none" && "用户只能领取一次此优惠券"}
                      {field.value === "weekly" && "用户每周可以重新领取一次此优惠券"}
                      {field.value === "monthly" && "用户每月可以重新领取一次此优惠券"}
                      {field.value === "yearly" && "用户每年可以重新领取一次此优惠券"}
                    </FormDescription>
                    <FormMessage />
                  </FormItem>
                )}
              />
            </div>


            <div className="flex gap-6">
              <FormField
                control={form.control}
                name="is_active"
                render={({ field }) => (
                  <FormItem className="flex items-center space-x-2 space-y-0">
                    <FormControl>
                      <Switch
                        checked={field.value}
                        onCheckedChange={field.onChange}
                      />
                    </FormControl>
                    <FormLabel className="!mt-0">激活状态</FormLabel>
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="visible"
                render={({ field }) => (
                  <FormItem className="flex items-center space-x-2 space-y-0">
                    <FormControl>
                      <Switch
                        checked={field.value}
                        onCheckedChange={field.onChange}
                      />
                    </FormControl>
                    <FormLabel className="!mt-0">前端可见</FormLabel>
                  </FormItem>
                )}
              />

              <FormField
                control={form.control}
                name="auto_link_activities"
                render={({ field }) => (
                  <FormItem className="flex items-center space-x-2 space-y-0">
                    <FormControl>
                      <Switch
                        checked={field.value}
                        onCheckedChange={field.onChange}
                      />
                    </FormControl>
                    <div className="flex flex-col">
                      <FormLabel className="!mt-0">自动关联活动</FormLabel>
                      <FormDescription className="text-xs">
                        开启后，新增活动将自动关联此优惠券
                      </FormDescription>
                    </div>
                  </FormItem>
                )}
              />
            </div>

            <div className="flex justify-end gap-2">
              <Button
                type="button"
                variant="outline"
                onClick={() => onOpenChange(false)}
                disabled={saveMutation.isPending}
              >
                {t("common.cancel")}
              </Button>
              <Button type="submit" disabled={saveMutation.isPending}>
                {saveMutation.isPending ? "保存中..." : "保存"}
              </Button>
            </div>
            </form>
          </Form>
        )}
      </DialogContent>
    </Dialog>
  );
}
